.iclock{
  position: relative;
  display: inline-block;
  width: 200px;
}
.iclock > .iclock-show{
  position: relative;
  width: 97%;
  height: 50px;
  line-height: 50px;
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-overflow:ellipsis;
  white-space: nowrap;
  cursor: pointer;
}
.iclock-time > .iclock-date, .iclock-week{
  position: absolute;
  top: -14px;
  font-size: 14px;
  -webkit-user-select: all;
     -moz-user-select: all;
      -ms-user-select: all;
          user-select: all;
  cursor: pointer;
}
.iclock-time > .iclock-date{
  right: 0;
}
.iclock-time > .iclock-week {
  left: 0;
}
.iclock-scroll{
  position: absolute;
  top: 5px;
  left: 15px;
  margin: 0;
}
.iclock-scroll span{
  font-size: 1.3rem;
  position: relative;
  top: -14px;
  font-weight: 900;
}
.iclock-scroll .num, .iclock-scroll span {
  margin-left: 5px;
}
.num {
  width: 11px;
  height: 40px;
  -o-object-fit: none;
     object-fit: none;
  -o-object-position: 0 0;
     object-position: 0 0;
  -webkit-transition: object-position .25s;
  transition: object-position .25s;
  transition: object-position .25s, -o-object-position .25s;
}
.num0 {
}
.num1 { -o-object-position: 0 -40px; object-position: 0 -40px;
}
.num2 { -o-object-position: 0 -80px; object-position: 0 -80px;
}
.num3 { -o-object-position: 0 -120px; object-position: 0 -120px;
}
.num4 { -o-object-position: 0 -160px; object-position: 0 -160px;
}
.num5 { -o-object-position: 0 -200px; object-position: 0 -200px;
}
.num6 { -o-object-position: 0 -240px; object-position: 0 -240px;
}
.num7 { -o-object-position: 0 -280px; object-position: 0 -280px;
}
.num8 { -o-object-position: 0 -320px; object-position: 0 -320px;
}
.num9 { -o-object-position: 0 -360px; object-position: 0 -360px;
}
.iclock > .iclock-body{
  position: relative;
  width: 100%;
  height: 100px;
  border-radius: 2%;
  border: 2px solid #22ade4;
  background-color: #fff;
  cursor: pointer;
}
/* .iclock-body:hover .iclock-mouse{
  animation: ppp 0.75s infinite;
  -webkit-animation: ppp 0.75s infinite;
} */
.iclock-body .glasses{
  position: absolute;
  top: -8px;
  left: 8px;
  z-index: 2;
}
.glasses-left, .glasses-right{
  position: absolute;
  top: 0;
  width: 70px;
  height: 30px;
  border: 3px solid #000;
  border-radius: 10%;
  display: inline-block;
  background-color: transparent;
}
.glasses > .glasses-mid{
  position: absolute;
  top: 30px;
  left: 60px;
  width: 40px;
  height: 3px;
  background-color: #000;
  display: inline-block;
}
.glasses > .glasses-left{
  left: 0;
}
.glasses >.glasses-right{
  left: 90px;
}
.iclock-left-eyes, .iclock-right-eyes{
  position: absolute;
  top: 15px;
  width: 60px;
  height: 6px;
  background-color: #22ade4;
}
.iclock-body > .iclock-left-eyes{
  left: 20px;
  transform: rotate(10deg);
  -webkit-transform: rotate(10deg);
}
.iclock-body > .iclock-right-eyes{
  right: 20px;
  transform: rotate(-10deg);
  -webkit-transform: rotate(-10deg);
}
.iclock-body > .iclock-right-box{
  position: absolute;
  top: 0;
  right: -50px;
  width: 50px;
  height: 100%;
  overflow: hidden;
}
.iclock-right-box > .iclock-cup{
  position: absolute;
  top: 20px;
  right: 0;
  border: 4px solid #22ade4;
  border-radius: 50%;
  width: 80px;
  height: 60px;
}
.iclock-body > .iclock-mouse{
  position: relative;
  left: 20px;
  top: 40px;
  width: 0;
  height: 0;
  overflow: hidden;
}
